/* Aqu√≠ van tus estilos */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', sans-serif;
    
}

.body{
    min-height: 100vh;
    display: grid;
    grid-template-rows: 200px 1fr;
    
    @media all and (min-width: 768px) {
        grid-template-rows: 250px 1fr;
    }
}

.page-not-found{
    margin-top: 200px;
    margin-left: 50px
}



